.header-img {
    width: 100%;
    height: 100vh;
    background-image: url(/static/media/photo-head.511e11fe.jpg);
    background-position: center;
    background-size: cover;
}

h1 {
    color: white !important;
    padding-top: 30%;
    font-size: 60px !important;
    text-shadow: 0px 0px 3px #000000;
}

.header-title {
    position: absolute;
    white-space: nowrap;
    -webkit-animation: floatTextLeft 2s ease-in-out;
            animation: floatTextLeft 2s ease-in-out;
}

@-webkit-keyframes floatTextLeft{
    from {
      left: 0%;
    }
    to {
      left: 390px;
    }
}

.header-img h2 {
    color: #4D83D3 !important;
    font-weight: 700 !important;
    text-shadow: 0px 0px 3px #ffffff !important;
    font-size: 50px !important;
    padding-top: 32.5%;
    position: absolute;
    white-space: nowrap;
}

.scroll-downs {
    position: absolute;
    right: 0;
    bottom: 30px;
    left: 0;
    margin: auto;
    width: 34px;
    height: 55px;
    z-index: 1;
}

.mousey {
    width: 1px;
    padding: 10px 10px;
    height: 25px;
    border: 2px solid #fff;
    border-radius: 25px;
    opacity: 0.75;
    box-sizing: content-box;
    box-shadow: 0px 1px 15px 6px rgba(0,0,0,0.75);
}

.scroller {
    width: 3px;
    height: 10px;
    border-radius: 25%;
    background-color: #fff;
    -webkit-animation-name: scroll;
            animation-name: scroll;
    -webkit-animation-duration: 2.2s;
            animation-duration: 2.2s;
    -webkit-animation-timing-function: cubic-bezier(.15,.41,.69,.94);
            animation-timing-function: cubic-bezier(.15,.41,.69,.94);
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes scroll {
    0% { opacity: 0; }
    10% { -webkit-transform: translateY(0); transform: translateY(0); opacity: 1; }
    100% { -webkit-transform: translateY(15px); transform: translateY(15px); opacity: 0;}
}

@keyframes scroll {
    0% { opacity: 0; }
    10% { -webkit-transform: translateY(0); transform: translateY(0); opacity: 1; }
    100% { -webkit-transform: translateY(15px); transform: translateY(15px); opacity: 0;}
}

