    right: 15px;
    bottom: 5px;
    background: #555;
    padding: 15px;
    text-align: center;
    margin: 0 0 0 0;
    cursor:pointer;
    transition: 0.5s;
    -moz-transition: 0.5s;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
}

.scroll:hover {
    background: #4D83D3;
    transition: 0.5s;
